<template>
  <div class="login-wrapper background-white">
    <div class="tab font-black">
      <router-link :to="{name:'login'}" class="tab-left font-green"></router-link>
      <router-link :to="{name:'register'}" class="tab-right font-black"></router-link>
      <hr class="selected-line" />
      <hr class="normal-line" />
    </div>
    <ValidationObserver ref="observer" v-slot="{ validate }">
      <form method="post" class="ui form">
        <ValidationProvider name="username" rules="required|email" v-slot="{ errors }">
          <div class="field">
            <label></label>
            <input type="text" v-model="username" placeholder="" />
          </div>
          <div class="error-message font-red">
            <p>{{ errors[0] }}</p>
          </div>
        </ValidationProvider>

        <ValidationProvider name="password" rules="required|max:16|min:6" v-slot="{ errors }">
          <div class="field">
            <label></label>
            <input type="password" v-model="password" placeholder="" />
          </div>
          <div class="error-message font-red">
            <p>{{ errors[0] }}</p>
          </div>
        </ValidationProvider>

        <ValidationProvider
          name="code"
          ref="codefield"
          rules="required|length:4"
          v-slot="{ errors }"
        >
          <div class="field">
            <label></label>
            <input type="text" v-model="code" placeholder="" />
          </div>
          <div class="error-message extra-length font-red">
            <p>{{ errors[0] }}</p>
          </div>
          <label class="code-message" @click="_getCode()" v-html="svg">{{svg}}</label>
        </ValidationProvider>

        <button
          type="button"
          class="ui green button main-button"
          @click="validate().then(submit)"
        ></button>
        <router-link :to="{name:'forget'}" class="forget-password-button font-black"></router-link>
      </form>
    </ValidationObserver>
  </div>
</template>
<script>
import { login } from '@/api/login'
import addPicURL from '@/util/addPicURL'
import codeMix from '@/mixin/code'

export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: '',
      errorMsg: [],
      value: ''
    }
  },
  mixins: [codeMix], // 
  methods: {
    async submit () {
      const isValid = await this.$refs.observer.validate()
      if (!isValid) {
        return
      }
      login({
        username: this.username,
        password: this.password,
        code: this.code,
        sid: this.$store.state.sid
      })
        .then(res => {
          console.log(res.data)
          if (res.code === 200) {
            res.data.pic = addPicURL(res.data.pic)
            console.log('submit -> res.data.pic', res.data.pic)
            this.$store.commit('setUserInfo', res.data)
            this.$store.commit('setIsLogin', true)
            this.$store.commit('setToken', res.token)
            this.username = ''
            this.password = ''
            this.code = ''
            requestAnimationFrame(() => {
              this.$refs.observer.reset()
            })
            this.$router.push({ name: 'index' })
          } else if (res.code === 401) {
            this.$refs.codefield.setErrors([res.msg])
          } else if (res.code === 404) {
            this.$alert('')
          }
        })
        .catch(err => {
          const data = err.response.data
          console.log(data)
          if (data.code === 404) {
            // it was 500
            this.$alert('')
          } else {
            this.$alert('')
          }
          console.log(err.response)
        })
    }
  },
  created () {}
}
</script>

