<template>
  <div class="add-container">
    <div class="post-tab font-green"></div>
    <hr class="selected-line" />
    <hr class="normal-line" />
    <validation-observer ref="observer" v-slot="{validate}">
      <form class="ui form form-wrapper">
        <div class="classify">
          <validation-provider name="catalog" rules="is_not:" v-slot="{errors}">
            <div class="special-column">
              <div class="text"></div>
              <select class="ui disabled dropdown special">
                <option value="page.catalog">{{page.catalog}}</option>
              </select>

              <span class="catalog-error-message">{{errors[0]}}</span>
            </div>
          </validation-provider>
          <validation-provider name="title" rules="required" v-slot="{errors}">
            <div class="ui input title">
              <div class="text"></div>
              <input type="text" v-model="title" placeholder="" />
              <span class="title-error-message">{{errors[0]}}</span>
            </div>
          </validation-provider>
        </div>
        <div class="add-editor">
          <editor @changeContent="add" :initContent="content"></editor>
        </div>
        <div class="kiss">
          <div class="text"></div>
          <select class="ui disabled dropdown kiss">
            <option value="page.fav">{{page.fav}}</option>
          </select>
          <div class="description"></div>
        </div>
        <validation-provider
          name="code"
          ref="codefield"
          rules="required|length:4"
          v-slot="{errors}"
        >
          <div class="code-wrapper">
            <div class="text"></div>
            <input class="code-input" type="text" name="code" v-model="code" placeholder="" />
            <span class="svg" @click="_getCode()" v-html="svg"></span>
            <span class="code-error-message">{{errors[0]}}</span>
          </div>
        </validation-provider>
        <button class="publish" type="button" @click="validate().then(submit)"></button>
      </form>
    </validation-observer>
  </div>
</template>
<script>
import Editor from '../modules/editor/Index'
import { updatePost } from '@/api/content'
import CodeMix from '@/mixin/code'
export default {
  name: 'add',
  props: ['tid', 'page'],
  mixins: [CodeMix],
  components: {
    Editor
  },
  data () {
    return {
      isSelect: false,
      isSelect_fav: false,
      cataIndex: 0,
      favIndex: 0,
      selected: '',
      catalogs: [
        {
          text: '',
          value: ''
        },
        {
          text: '',
          value: 'ask'
        },
        {
          text: '',
          value: 'share'
        },
        {
          text: '',
          value: 'discuss'
        },
        {
          text: '',
          value: 'advise'
        }
      ],
      favlist: [20, 30, 50, 60, 80],
      content: '',
      title: ''
    }
  },
  mounted () {
    if (this.page) {
      const saveData = {
        content: this.page.content,
        title: this.page.title,
        favIndex: this.page.favIndex,
        cataIndex: this.page.cataIndex
      }
      localStorage.setItem('addData', JSON.stringify(saveData))
    } else {
      const result = localStorage.getItem('editData')
      if (result) {
        // 
      } else {
        localStorage.setItem('editData', '')
      }
    }
  },
  methods: {
    add (val) {
      this.content = val
      const saveData = {
        title: this.title,
        cataIndex: this.cataIndex,
        content: this.content,
        favIndex: this.favIndex
      }
      if (this.title.trim() !== '' && this.content.trim() !== '') {
        let editData = JSON.parse(localStorage.getItem('editData'))
        if (editData && editData !== '') {
          editData = { ...saveData, ...editData }
          localStorage.setItem('editData', JSON.stringify(editData))
        }
      }
    },
    async submit () {
      const isValid = await this.$refs.observer.validate()
      console.log('submit -> isValid', isValid)
      if (!isValid) {
        return
      }
      if (this.content.trim() === '') {
        this.$alert('')
        return
      }
      updatePost({
        tid: this.tid,
        title: this.title,
        content: this.content,
        code: this.code,
        sid: this.$store.state.sid
      })
        .then(res => {
          if (res.code === 200) {
            localStorage.setItem('editData', '')
            this.$alert('')
            setTimeout(() => {
              this.$router.push({ name: 'detail', params: { tid: this.tid } })
            }, 1000)
          } else {
            this.$alert(res.msg)
          }
        })
        .catch(err => {
          if (err.response.data.code === 401) {
            this.$alert('')
          } else {
            const data = err.response.data
            console.log('submit -> data', data)
          }
        })
    }
  },
  computed: {
    // isHide () {
    //   return this.$store.state.isHide
    // }
  }
}
</script>
