<template>
  <div class="add-container">
    <div class="post-tab font-green"></div>
    <hr class="selected-line" />
    <hr class="normal-line" />
    <validation-observer ref="observer" v-slot="{validate}">
      <form class="ui form form-wrapper">
        <div class="classify">
          <validation-provider name="catalog" rules="is_not:" v-slot="{errors}">
            <div class="special-column">
              <!-- <div class="ui dropdown labeled"> -->
              <div class="text"></div>
              <select class="ui dropdown special" v-model="selected">
                <!---->
                <option
                  v-for="(catalog,index) in catalogs"
                  :key="'catalog'+index"
                  :value="catalog.value"
                >{{catalog.text}}</option>
              </select>

              <span class="catalog-error-message">{{errors[0]}}</span>
            </div>
          </validation-provider>
          <validation-provider name="title" rules="required" v-slot="{errors}">
            <div class="ui input title">
              <div class="text"></div>
              <input type="text" v-model="title" placeholder="" />
              <span class="title-error-message">{{errors[0]}}</span>
            </div>
          </validation-provider>
        </div>
        <div class="add-editor">
          <editor @changeContent="add" :initContent="content"></editor>
        </div>
        <div class="kiss">
          <div class="text"></div>
          <select class="ui dropdown kiss" v-model="favlist[favIndex]">
            <option v-for="(fav,index) in favlist" :key="'fav'+index" :value="fav">{{fav}}</option>
          </select>
          <div class="description"></div>
        </div>
        <validation-provider
          name="code"
          ref="codefield"
          rules="required|length:4"
          v-slot="{errors}"
        >
          <div class="code-wrapper">
            <div class="text"></div>
            <input class="code-input" type="text" name="code" v-model="code" placeholder="" />
            <span class="svg" @click="_getCode()" v-html="svg"></span>
            <span class="code-error-message">{{errors[0]}}</span>
          </div>
        </validation-provider>
        <button class="publish" type="button" @click="validate().then(submit)"></button>
      </form>
    </validation-observer>
  </div>
</template>
<script>
import Editor from '../modules/editor/Index'
import { addPost } from '@/api/content'
import CodeMix from '@/mixin/code'
export default {
  name: 'add',
  mixins: [CodeMix],
  components: {
    Editor
  },
  data () {
    return {
      isSelect: false,
      isSelect_fav: false,
      cataIndex: 0,
      favIndex: 0,
      selected: '',
      catalogs: [
        {
          text: '',
          value: ''
        },
        {
          text: '',
          value: 'ask'
        },
        {
          text: '',
          value: 'share'
        },
        {
          text: '',
          value: 'discuss'
        },
        {
          text: '',
          value: 'advise'
        }
      ],
      favlist: [20, 30, 50, 60, 80],
      content: '',
      title: ''
    }
  },
  mounted () {
    const saveData = localStorage.getItem('addData')
    if (saveData && saveData !== '') {
      this.$confirm(
        '',
        () => {
          const obj = JSON.parse(saveData)
          this.content = obj.content
          this.title = obj.title
          this.cataIndex = obj.cataIndex
          this.favIndex = obj.favIndex
        },
        () => {
          localStorage.setItem('addData', '')
        }
      )
    }
  },
  methods: {
    // chooseCatalog (item, index) {
    //   this.cataIndex = index
    // },
    // chooseFav (item, index) {
    //   this.favIndex = index
    // },
    // changeSelect () {
    //   this.isSelect = !this.isSelect
    // },
    // changeFav () {
    //   this.isSelect_fav = !this.isSelect
    // },
    add (val) {
      this.content = val
      const saveData = {
        title: this.title,
        cataIndex: this.cataIndex,
        content: this.content,
        favIndex: this.favIndex
      }
      if (this.title.trim() !== '' && this.content.trim() !== '') {
        localStorage.setItem('addData', JSON.stringify(saveData))
      }
    },
    async submit () {
      const isValid = await this.$refs.observer.validate()
      console.log('submit -> isValid', isValid)
      if (!isValid) {
        return
      }
      if (this.content.trim() === '') {
        this.$alert('')
        return
      }
      console.log(
        'submit -> title',
        this.title,
        this.code,
        this.$store.state.sid,
        this.cataIndex,
        // this.catalogs[this.cataIndex].value,
        this.selected,
        this.favlist[this.favIndex]
      )
      addPost({
        title: this.title,
        catalog: this.selected,
        content: this.content,
        fav: this.favlist[this.favIndex],
        code: this.code,
        sid: this.$store.state.sid
      })
        .then(res => {
          console.log('submit -> res', res)
          if (res.code === 200) {
            localStorage.setItem('addData', '')
            this.$alert('2s')
            setTimeout(() => {
              this.$router.push({ name: 'index' })
            }, 2000)
          } else {
            this.$alert(res.msg)
          }
        })
        .catch(err => {
          if (err.response.data.code === 401) {
            this.$alert('')
          } else {
            const data = err.response.data
            console.log('submit -> data', data)
          }
        })
    }
  },
  computed: {
    // isHide () {
    //   return this.$store.state.isHide
    // }
  }
}
</script>
